<template>
    <div class="login-panel">
        <router-link to="/">
            <img src="@/assets/img/logo-hotel.png" alt="logo">
        </router-link>
        <h3>Logowanie</h3>
        <md-field class="panel-input">
            <label>E-mail</label>
            <md-input v-model="user.email"></md-input>
        </md-field>
        <md-field class="panel-input">
            <label>Hasło</label>
            <md-input v-model="user.haslo" type="password"></md-input>
        </md-field>
        <div class="submit-box">
            <router-link to="/auth/reset">
                Zapomniałeś hasła?
            </router-link>
            <button @click="checkAuth">
                Zaloguj się
            </button>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    data() {
        return {
            user: {
                email: '',
                haslo: ''
            }
        }
    },
    methods: {
        checkAuth() {
            axios.post('https://hotel-back.cleverapps.io/login-user', {
                user: this.user
            })
            .then(res => {
                if (res.data.email == 'pracownik' && res.data.haslo == '123') {
                    this.$router.push('/panel');
                }
            })
        }
    }
}
</script>
